<template>
  <div>
    <!-- 订单页面start -->
    <div class="orderform" @click="log">
      <ul>
        <li
          v-for="(item, index) in $store.state.order.lists"
          :key="index"
          v-show="item.type == 2"
        >
          <div class="orderform_top">
            <div class="oft_left">
              <img :src="item.storeImg" alt="" />
              <ul>
                <li>
                  <span class="oftl_content">
                    {{ item.storeName }}
                  </span>
                  <span class="oftl_icon">
                    <van-icon name="arrow" />
                  </span>
                </li>
                <li>
                  {{ item.dataTime | dataFormat | dataText }}
                </li>
              </ul>
            </div>
            <div class="oft_right">
              待送达
            </div>
          </div>
          <div class="orderform_bottom">
            <div
              class="ofb_left"
              v-for="(obj, key) in item.goodsList"
              :key="key"
            >
              {{ obj.name }}
            </div>
            <div class="ofb_right">￥{{ item.price }}</div>
          </div>
        </li>
      </ul>
    </div>
    <!-- 订单页面end -->
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    log() {
      console.log(this.$store.state.order.lists);
      this.$store.state.order.lists.forEach(item => {
        console.log("item", item);
        console.log("goodlist", item.goodsList);
      });
    }
  },
  created() {
    this.$store.state.vanTabbar = true;
  },
  beforeDestroy() {
    this.$store.state.vanTabbar = true;
  },
  filters: {
    dataFormat(dataString) {
      console.log(dataString);

      var dd = new Date();

      // 获取 年 月 日
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1;
      var d = dd.getDate();

      // 获取 时 分 秒
      var hh = dd.getHours();
      var mm = dd.getMinutes();
      var ss = dd.getSeconds();

      return `${y}-${m}-${d} ${hh}:${mm}:${ss}`;
    },
    dataText(data) {
      var arr = ["天", "一", "二", "三", "四", "五", "六"];
      var week = new Date().getDay();
      return data + "星期" + arr[week];
    }
  }
};
</script>

<style lang="scss" scoped>
body {
  background-color: #f2f2f2;
}
// 订单页面样式start
.orderform {
  width: 100%;
  max-width: 436px;
  min-width: 320px;
  margin: 10px auto;
  & > ul {
    width: 100%;
    & > li {
      height: 120px;
      background-color: #fff;
      position: relative;
      .orderform_top {
        height: 60px;
        // background-color: pink;
        .oft_left {
          & > img {
            position: absolute;
            width: 30px;
            left: 15px;
            top: 15px;
          }
          & > ul {
            float: left;
            // margin-top: 15px;
            // margin-left: 5px;
            position: absolute;
            left: 50px;
            top: 15px;
            & > li {
              height: 18px;
              font-size: 12px;
              color: #999999;
              line-height: 18px;
              .oftl_content {
                width: 80px;
                font-size: 16px;
                color: #333;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
              .oftl_icon {
                font-size: 12px;
                color: #999999;
                margin-left: 5px;
              }
            }
          }
        }
        .oft_right {
          width: 50px;
          height: 60px;
          text-align: center;
          line-height: 60px;
          font-size: 14px;
          color: #333;
          position: absolute;
          right: 10px;
          top: 0px;
        }
      }
      .orderform_bottom {
        // background-color: skyblue;
        height: 60px;
        border-top: 1px solid #f0f0f0;
        box-sizing: border-box;
        .ofb_left {
          float: left;
          height: 60px;
          line-height: 60px;
          font-size: 14px;
          margin-left: 10px;
        }
        .ofb_right {
          float: right;
          height: 60px;
          line-height: 60px;
          font-size: 16px;
          font-weight: bold;
          margin-right: 20px;
        }
      }
    }
  }
}
// 订单页面样式end
</style>
